.LayerItems {
  .image-wrapper {

    position: relative;
    width: 100px;
    height: 100px;
    border: 1px solid black;
    vertical-align: middle;

    img {
      width: 100%;
    }
  }
  .image-container {

    display: inline-block;
    margin-right: 10px;
    margin-bottom: 10px;

    button {
      width: 100%;
      cursor: pointer;
    }
  }

  .dropzone {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    border-width: 2px;
    border-radius: 2px;
    border-color: #eeeeee;
    border-style: dashed;
    background-color: #fafafa;
    color: #bdbdbd;
    outline: none;
    transition: border .24s ease-in-out;
    font-family: "Lato", Helvetica, Arial, sans-serif;
    cursor: pointer;
  }
}
